import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, RangeControl, SelectControl, __experimentalUnitControl as UnitControl, __experimentalNumberControl as NumberControl, Tooltip, Button, Dashicon, CheckboxControl, ToggleControl } from '@wordpress/components';
import { produce } from 'immer';

// Settings Components
import { Label, Background, BColor, BDevice, BorderControl, ColorsControl, HelpPanel, InlineDetailMediaUpload, SpaceControl, Typography } from '../../Components';
import { gearIcon } from '../../Components/utils/icons';
import { pxUnit, perUnit, emUnit } from '../../Components/utils/options';

import { generalStyleTabs, itemHoverEffects, titleStyles } from './utils/options';

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex, setIsTitleEdit }) => {
	const { categories, portfolios, columns, columnGap, rowGap, background, padding, border, filterBtnTypo, filterBtnColors, filterBtnHoverColors, itemHeight, itemHoverEffect, isLink, titleStyle, titleBG, titleTypo, titleColor } = attributes;

	const [device, setDevice] = useState('desktop');

	const addPortfolio = () => {
		setAttributes({
			portfolios: [...portfolios, {
				image: { id: null, url: '', alt: '', title: '' },
				title: `Portfolio ${portfolios.length + 1}`,
				link: '#',
				cats: []
			}]
		});

		setActiveIndex(portfolios.length);
	}

	const updatePortfolio = (type, val, otherIndex = null) => {
		const newPortfolios = produce(portfolios, draft => {
			if (null !== otherIndex) {
				draft[activeIndex][type][otherIndex] = val;
			} else {
				draft[activeIndex][type] = val;
			}
		});
		setAttributes({ portfolios: newPortfolios });
	}

	const duplicatePortfolio = e => {
		e.preventDefault();

		setAttributes({ portfolios: [...portfolios.slice(0, activeIndex), { ...portfolios[activeIndex] }, ...portfolios.slice(activeIndex)] });

		setActiveIndex(activeIndex + 1);
	}

	const removePortfolio = e => {
		e.preventDefault();

		setAttributes({ portfolios: [...portfolios.slice(0, activeIndex), ...portfolios.slice(activeIndex + 1)] });

		setActiveIndex(0 === activeIndex ? 0 : activeIndex - 1);
	}

	const { image = {}, title = '', link = '', cats = [] } = portfolios[activeIndex] || {};

	return <InspectorControls>
		<div className='pfbInspectorInfo'>
			Need more block like this? Checkout the bundle  <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
		</div>

		<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
			{'general' === tab.name && <>
				<HelpPanel slug='portfolio-block' docsLink='https://bblockswp.com/docs/portfolio-block' />


				<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Categories', 'portfolio-block')}>
					{categories.map((category, index) => <PanelRow key={index}>
						<Label className=''>{__(`Cat ${index + 1}:`, 'portfolio-block')}</Label>
						<TextControl value={category} onChange={val => {
							const newCategories = [...categories];
							newCategories[index] = val;
							setAttributes({ categories: newCategories });
						}} />

						<Tooltip text={__('Remove this category', 'portfolio-block')} position='top center'>
							<Button className='removeCategory' onClick={e => {
								e.preventDefault();
								setAttributes({ categories: [...categories.slice(0, index), ...categories.slice(index + 1)] });
							}} ><Dashicon icon='no' /></Button>
						</Tooltip>
					</PanelRow>)}

					<div className='addItem mt15'>
						<Button label={__('Add New Category', 'portfolio-block')} onClick={() => setAttributes({ categories: [...categories, ''] })}><Dashicon icon='plus' />{__('Add New Category', 'portfolio-block')}</Button>
					</div>
				</PanelBody>


				<PanelBody className='bPlPanelBody addRemoveItems editItem' title={__('Add or Remove Portfolio', 'portfolio-block')}>
					{null !== activeIndex && <>
						<h3 className='bplItemTitle'>{__(`Portfolio No ${activeIndex + 1}:`, 'portfolio-block')}</h3>

						<Label>{__('Image:', 'portfolio-block')}</Label>
						<InlineDetailMediaUpload value={image} onChange={val => updatePortfolio('image', val)} placeholder={__('Enter Image URL', 'portfolio-block')} />

						<PanelRow className='mt15'>
							<Label className=''>{__('Title:', 'portfolio-block')}</Label>
							<TextControl value={title} onChange={val => updatePortfolio('title', val)} />
						</PanelRow>

						<PanelRow className='mt15'>
							<Label className=''>{__('Link:', 'portfolio-block')}</Label>
							<TextControl value={link} onChange={val => updatePortfolio('link', val)} />
						</PanelRow>

						<Label>{__('Select Categories:', 'portfolio-block')}</Label>
						{categories.map(cat => {
							const isInc = cats.includes(cat);

							return <CheckboxControl label={cat} key={cat} checked={isInc} onChange={val => {
								updatePortfolio('cats', val ? [...cats, cat] : cats.filter(id => id !== cat));
							}} />;
						})}

						<PanelRow className='itemAction mt20 mb15'>
							{1 < portfolios?.length && <Button className='removeItem' label={__('Remove', 'portfolio-block')} onClick={removePortfolio} ><Dashicon icon='no' />{__('Remove', 'portfolio-block')}</Button>}

							<Button className='duplicateItem' label={__('Duplicate', 'portfolio-block')} onClick={duplicatePortfolio} >{gearIcon}{__('Duplicate', 'portfolio-block')}</Button>
						</PanelRow>
					</>}

					<div className='addItem'>
						<Button label={__('Add New Portfolio', 'portfolio-block')} onClick={addPortfolio}><Dashicon icon='plus' />{__('Add New Portfolio', 'portfolio-block')}</Button>
					</div>
				</PanelBody>


				<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'portfolio-block')} initialOpen={false}>
					<PanelRow>
						<Label className='mb5'>{__('Columns:', 'portfolio-block')}</Label>
						<BDevice device={device} onChange={val => setDevice(val)} />
					</PanelRow>
					<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

					<NumberControl className='mt20' label={__('Column Gap:', 'portfolio-block')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: parseInt(val) })} />

					<NumberControl className='mt20' label={__('Row Gap:', 'portfolio-block')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: parseInt(val) })} />
				</PanelBody>
			</>}


			{'style' === tab.name && <>
				<PanelBody className='bPlPanelBody' title={__('Portfolio', 'portfolio')}>
					<Background label={__('Background:', 'portfolio')} value={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#fff' }} />

					<SpaceControl className='mt20' label={__('Padding:', 'portfolio')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '10px' }} />

					<BorderControl className='mt20' label={__('Border:', 'portfolio')} value={border} onChange={val => setAttributes({ border: val })} defaults={{ radius: '5px' }} />
				</PanelBody>


				<PanelBody className='bPlPanelBody' title={__('Filter', 'portfolio-block')} initialOpen={false}>
					<Typography label={__('Button Typography:', 'portfolio-block')} value={filterBtnTypo} onChange={val => setAttributes({ filterBtnTypo: val })} defaults={{ fontSize: { desktop: 14, tablet: 14, mobile: 14 } }} />

					<ColorsControl label={__('Button Colors')} value={filterBtnColors} onChange={val => setAttributes({ filterBtnColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

					<ColorsControl label={__('Button Hover/Active Colors')} value={filterBtnHoverColors} onChange={val => setAttributes({ filterBtnHoverColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} />
				</PanelBody>


				<PanelBody className='bPlPanelBody' title={__('Item', 'portfolio-block')} initialOpen={false}>
					<UnitControl label={__('Height:', 'portfolio-block')} labelPosition='left' value={itemHeight} onChange={val => setAttributes({ itemHeight: val })} units={[pxUnit(), perUnit(), emUnit()]} />

					<PanelRow className='mt20'>
						<Label className=''>{__('Hover Effect:', 'portfolio-block')}</Label>

						<SelectControl value={itemHoverEffect} onChange={val => setAttributes({ itemHoverEffect: val })} options={itemHoverEffects} />
					</PanelRow>

					<ToggleControl className='mt20' label={__('Enable Link in preview', 'skype-button')} checked={isLink} onChange={val => setAttributes({ isLink: val })} />
				</PanelBody>


				<PanelBody className='bPlPanelBody' title={__('Title', 'portfolio-block')} initialOpen={false} onToggle={e => setIsTitleEdit(e)}>
					<PanelRow>
						<Label className=''>{__('Title Styles:', 'portfolio-block')}</Label>

						<SelectControl value={titleStyle} onChange={val => setAttributes({
							titleStyle: val,
							titleBG: 'overlayTop' === val ? { type: 'gradient', gradient: 'linear-gradient(0deg, #0000 30%, #000000d9)' } : 'overlayBottom' === val ? { type: 'gradient', gradient: 'linear-gradient(180deg, #0000 30%, #000000d9)' } : { type: 'solid', color: '#0006' }
						})} options={titleStyles} />
					</PanelRow>

					<Background label={__('Background Overlay:', 'portfolio-block')} value={titleBG} onChange={val => setAttributes({ titleBG: val })} defaults={{ color: '#0006' }} isImage={false} />

					<Typography label={__('Typography:', 'portfolio-block')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 22, tablet: 20, mobile: 18 } }} />

					<BColor label={__('Color:', 'portfolio-block')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#fff' />
				</PanelBody>
			</>}
		</>}</TabPanel>
	</InspectorControls>;
};
export default Settings;