import { useEffect, useState } from 'react';

import Settings from './Settings';
import Portfolio from './Portfolio';
import camelCase from './utils/camelCase';

const Edit = props => {
	const { className, attributes, setAttributes, clientId } = props;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	const [activeIndex, setActiveIndex] = useState(0);
	const [isTitleEdit, setIsTitleEdit] = useState(false);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} activeIndex={activeIndex} setActiveIndex={setActiveIndex} setIsTitleEdit={setIsTitleEdit} />

		<div className={className} id={`pfbPortfolio-${clientId}`}>
			<Portfolio attributes={attributes} clientId={clientId} isTitleEdit={isTitleEdit}>
				<Gallery attributes={attributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />
			</Portfolio>
		</div>
	</>;
};
export default Edit;

const Gallery = ({ attributes, clientId, activeIndex, setActiveIndex }) => {
	const { portfolios, itemHoverEffect, titleStyle } = attributes;

	return <div id={`gallery-${clientId}`} className='gallery isotopeNotInit'>
		{portfolios?.map((item, index) => {
			const { image, title, cats } = item;

			return <div
				key={index}
				onClick={() => setActiveIndex(index)}
				className={`galleryItem ${cats?.map(cat => camelCase(cat)).join(' ')} ${index === activeIndex ? 'pfbNowEditing' : ''}`}
			>
				<figure className={`galleryFigure ${itemHoverEffect}`}>
					<img src={image?.url} alt={image?.alt || image?.title} />
					{<figcaption className={titleStyle}><p>{title}</p></figcaption>}
				</figure>
			</div>;
		})}
	</div>
}